body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  }
  
  header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  }
  
  main {
  padding-top: 64px; 
  padding-bottom: 56px;  
  min-height: calc(100vh - 120px); 
  }
   /* adjust this value to match your header height 
   adjust this value to match your footer height 
   adjust this value to match your header and footer height  */
  
  footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 1;
  }